
type CardAttributes = {
    name?: string
    cost?: string
    type?: string
    text?: string
}

export class ActionCard extends HTMLElement {

    constructor() {
        super()
        this.innerHTML = this.render()
    }

    static get observedAttributes() { return ["name", "cost", "type", "text"]; }

    private cardAttributes: CardAttributes = {}

    attributeChangedCallback(attrName: keyof CardAttributes, oldVal: string, newVal: string) {
        console.log(attrName, oldVal, newVal)
        this.cardAttributes[attrName] = newVal
        this.innerHTML = this.render()
    }

    private render = () => `
    <div>
        <div class="card">
            <div class="inner-card">
                <div class="card-headline">
                    <div>${this.cardAttributes.name}</div>
                    <div>${this.cardAttributes.cost}</div>
                </div>
                <div class="border" style="padding: 0.5em; flex: 0 0 40%;">
                    card image
                </div>
                <div style="padding-left: 0.5em; padding-bottom: 2px; flex: 0 0;">
                    ${this.cardAttributes.type}
                </div>
                <div class="border" style="padding: 0.5em; flex: 0 0 35%; overflow: auto;">
                    ${this.cardAttributes.text}
                </div>
            </div>
            <div class="card-arrow"></div>
        </div>
    </div>
    `
}

window.customElements.define("action-card", ActionCard)
